@import url('https://fonts.googleapis.com/css?family=Poppins:300,400&display=swap');

$blue: #1b88c7;
$red: #e20060;
$white: #fff;
$grey: #898989;
$light-grey:#e1e1e1;
$green: #09a61e;

.wfw-shortcode-default {
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  width: 338px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;

  @media screen and (max-width: 380px) {
    width: 100%;
  }

  .text-center {
    text-align: center;
  }

  .wfw-form {
    width: 100%;
  }

  .wfw-group {
    label {
      display: block;
      font-size: 12px;
      font-weight: 300;
      color: $grey;
      text-transform: uppercase;
    }

    .wfw-control {
      width: 100%;
      font-family: 'Poppins', sans-serif;
      border-top: 0;
      border-right: 0;
      border-left: 0;
      border-bottom: 1px solid $light-grey;
      font-size: 16px;
      padding: 0 0 9px 0;
      line-height: 24px;
      height: 28px;
      margin-bottom: 25px;
      box-shadow: none;
      outline: none;
      transition: all .3s ease;
      margin-top: 10px;
    }

    .wfw-normal {
      input {
        margin-bottom: 25px;
        display: initial;
      }
    }

    .error-text {
      position: absolute;
      left: 0;
      font-size: 12px;
      bottom: 7px;
      color: $red;
      opacity: 0;
      visibility: hidden;
      transition: all .3s ease;
    }

    .wfw-required {
      position: relative;

      input {
        margin-bottom: 25px;
        display: initial;
      }

      &::before {
        content: "";
        width: 0;
        background-color: $red;
        height: 3px;
        display: block;
        bottom: 25px;
        position: absolute;
        transition: width .3s ease;
      }

      &.invalid {
        input {
          color: $red;
          padding-right: 15px;
          display: initial;
        }

        .error-text {
          opacity: 1;
          visibility: visible;
        }

        &::after {
          content: "*";
          color: $red;
          right: 0;
          position: absolute;
          top: 4px;
        }

        &::before {
          content: "";
          width: 100%;
        }
      }
    }
  }

  .wfw-checkbox.invalid {
    .custom-checkbox {
      position: absolute;
      top: 0;
      bottom: 0;
      margin-top: auto;
      margin-bottom: auto;
      left: -23px;
      display: block;
      height: 15px;
      width: 15px;
      border: 1px solid $red;
      border-radius: 2px;
    }
  }
  .wfw-checkbox {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    position: relative;

    .custom-checkbox {
      position: absolute;
      top: 0;
      bottom: 0;
      margin-top: auto;
      margin-bottom: auto;
      left: -23px;
      display: block;
      height: 15px;
      width: 15px;
      border: 1px solid #e1e1e1;
      border-radius: 2px;
      transition: all .3s ease;
      z-index: 98;

      &:after {
        content: "";
        position: absolute;
        opacity: 0;
        background-image: url('../images/tick.svg');
        height: 9px;
        width: 9px;
        top: 1px;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        transition: all .3s ease;
      }
    }

    input[type="checkbox"]:checked ~ .custom-checkbox:after {
      opacity: 1;
    }

    input[type="checkbox"] {
      position: absolute;
      opacity: 0;
      cursor: pointer;
      height: 17px;
      width: 17px;
      top: 0;
      bottom: 0;
      margin: auto;
      left: -23px;
      z-index: 99;
    }
  }

  input[type="submit"] {
    margin: auto;
    border-radius: 15px;
    display: block;
    text-transform: uppercase;
    color: $white;
    font-size: 18px;
    line-height: 40px;
    height: 40px;
    width: 276px;
    outline: none;
    transition: all .3s ease;
    padding: unset;
    font-weight: normal;

    &:hover {
      text-decoration: none;
    }

    &:focus {
      text-decoration: none;
    }
  }

  .alert-success {
    p {
      margin: 0 auto 10px;
      text-align: center;
      position: relative;
      color: $green;
      font-size: 12px;

      &:before {
        content: "";
        background-image: url('../images/tick.svg');
        height: 9px;
        width: 9px;
        position: absolute;
        margin: 5px 0 0 -17px;
        top: 0;
        bottom: 0;
      }
    }

  }

  .alert-danger {
    p {
      margin: 0 auto 10px;
      color: $red;
      text-align: center;
      position: relative;
      font-size: 12px;

      &:before {
        content: "*";
        color: $red;
        margin-right: 3px;
      }

    }
  }

  .wfw-privacy_policy {
    display: flex;
    justify-content: center;
    margin-top: 24px;
    font-weight: 300;
    font-size: 12px;

    p {
      margin: 0 0 0 5px;
    }

    a {
      text-decoration: underline;
      color: $blue;
      &:hover{
        text-decoration: none
      }
    }
  }

}
